.examCardContainer {
  width: 94%;
  height: 94%;
  /* margin: auto; */
  margin-left: 3%;
  margin-right: 3%;
  margin-bottom: 3%;
  margin-top: 3%;
  box-shadow: 5px 10px 18px #888888;
  background-color: ghostwhite;
  background-color: #fcfdfd;
  display: flex;
}

.progressBar {
  width: 350px;
  margin: auto;
  margin-top: 20%;
}

@media screen and (min-width: 501px) {
  .innerContainer {
    background-color: #d1d6da;
    width: 80%;
  }
}

@media screen and (max-width: 500px) {
  .innerContainer {
    background-color: #d1d6da;
    width: 100%;
  }
}

.profilePageContainer {
  background: linear-gradient(
    180deg,
    rgba(27, 172, 184, 1) 0%,
    rgba(26, 196, 162, 1) 100%
  );
  height: 100%;
  display: flex;
  width: 96%;
  border-radius: 10px;
  margin-top: 2%;
  margin-bottom: 2%;
  margin-left: 2%;
  margin-right: 2%;
}

.spinnerPostion {
  position: fixed;
  top: 50%;
  left: 50%;
}
